import { Component, OnInit } from '@angular/core';
import { DefaultService, Transaction } from '../../api-client';
import { FormBuilder, FormArray, FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'coc-charge-list',
  templateUrl: './charge-list.component.html',
  styleUrls: ['./charge-list.component.scss']
})
export class ChargeListComponent implements OnInit {

  public form: FormGroup;
  public transactions: Transaction[];

  constructor(private apiClient: DefaultService,
    private formBuilder: FormBuilder) {
      this.form = this.formBuilder.group({
        transactions: new FormArray([])
      });
  }

  ngOnInit() {

    this.loadCharges();

  }

  public submit() {


    (this.form.controls.transactions as FormArray).controls.forEach((item, index) => {

      if (item.value) {
        console.log(`Transaction ${this.transactions[index].TRXN_ID} checked for payment`);
      }

    });

    for (const control of (this.form.controls.transactions as FormArray).controls) {

        if (control.value) {

        }
    }
  }

  private loadCharges() {

    this.apiClient.transactionsGet()
      .subscribe(
        (transactions) => {

          this.transactions = transactions;

          transactions.map(() => {
            const control = new FormControl(true);
            (this.form.controls.transactions as FormArray).push(control);
          });
        }
      );
  }
}
